import React from "react";
import assert from "@brillout/assert";
import { page_config } from "../page_config";
import { ActivationPage } from "./ActivationPage";

export { activationPageConfig };

function activationPageConfig(props) {
  assert_props(props);
  const { projectName, npmName } = props;
  const route = "/npm/" + npmName + "/activate";
  const view = (props) => <ActivationPage {...props} />;
  return page_config(view, projectName, {
    route,
    no_header: true,
    hydrateReact: true,
    addInitialProps: () => props,
  });
}

function assert_props(props) {
  assert(props.projectName);
  assert(props.npmName);
  assert(props.pricing);
}
